.header{font-family:'Libre Caslon Text', serif;margin-top:20px;margin-left:100px;margin-right:100px;background-image:url(/ruleta-avinka/static/media/FONDO.7f6a8aa0.png);color:#18643B;border-radius:.5em;display:flex;flex-direction:column;align-items:center;text-align:center;border:10px solid #18643B;box-shadow:10px 10px 42px 0px rgba(0,0,0,0.75)}.header h1{font-size:35px;text-transform:uppercase}.header img{margin-top:-30px;width:400px}.avinkaHeader{margin-top:-20px;display:flex;justify-content:center;flex-wrap:wrap}.avinkaHeader img{margin-top:9px;width:200px;height:60px}.avinkaHeader h1{font-size:35px;text-transform:uppercase}.timer-text{background-image:url(/ruleta-avinka/static/media/FONDO.7f6a8aa0.png);margin-top:20px;margin-right:20px;margin-left:20px;margin-bottom:20px;border:2px solid #ecf0ed;border-radius:15px;text-align:center;color:#1B5A4F}body{display:flex;justify-content:center;align-items:center;min-height:100vh}.app{font-family:'Poppins', sans-serif;background-image:url(/ruleta-avinka/static/media/FONDO.7f6a8aa0.png);width:590px;min-height:400px;height:-webkit-min-content;height:-moz-min-content;height:min-content;border-radius:15px;padding:20px;box-shadow:10px 10px 42px 0px rgba(0,0,0,0.75);display:flex;justify-content:space-evenly}.score-section{display:flex;font-size:24px;align-items:center}.question-section{width:100%;position:relative}.question-count{text-align:center;margin-bottom:20px;color:white}.question-count span{font-size:28px}.question-text{margin-top:20px;margin-bottom:20px;margin-left:10px;margin-right:10px;color:#1B5A4F;font-size:25px}.answer-section{width:100%;display:flex;flex-direction:column;justify-content:space-between}.answer-section a{text-decoration:none}button{margin-top:20px;margin-bottom:20px;font-family:'Poppins', sans-serif;width:100%;font-size:20px;color:#ffffff;background-color:#1B5A4F;border-radius:10px;display:flex;padding:20px;justify-content:flex-start;align-items:center;cursor:pointer}.correct{background-color:#2f922f}.incorrect{background-color:#ff3333}button:hover{background-color:#5d8867}button:focus{outline:none}button svg{margin-right:5px}.artisan-logo{margin-top:50px;margin-bottom:50px}.bodyRoulette{display:flex;flex-direction:column;align-items:center;text-align:center}.element{margin-top:-25px;width:450px;transform:rotate(0deg);-webkit-animation-name:spin;animation-name:spin;-webkit-animation-duration:5000ms;animation-duration:5000ms}.marker{margin-top:-5px;position:relative;top:50px;margin-right:13px;z-index:2}.button{margin-top:20px;width:300px;cursor:pointer}.winner{font-family:'Poppins', sans-serif;background-image:url(/ruleta-avinka/static/media/Fondo-winner.e4a8415c.png);width:650px;min-height:500px;height:-webkit-min-content;height:-moz-min-content;height:min-content;border-radius:15px;color:#18643B;font-size:25px;padding:20px;box-shadow:10px 10px 42px 0px rgba(0,0,0,0.75);display:flex;justify-content:space-evenly}.miniwinner{width:300px}.cupon{width:300px;margin-left:-500px}a{text-decoration:none}.restart-game{font-family:'Poppins', sans-serif;width:200px;margin-top:30px;margin-left:490px;font-size:24.5px;color:#ffffff;background-color:#1B5A4F;border-radius:10px;display:flex;padding:20px;cursor:pointer}.loser{background-image:url(/ruleta-avinka/static/media/FONDO.7f6a8aa0.png);width:650px;font-family:'Poppins', sans-serif;display:flex;justify-content:center;align-items:center;border-radius:15px;color:#1B5A4F;font-size:22.5px;padding:20px;box-shadow:10px 10px 42px 0px rgba(0,0,0,0.75);flex-direction:column}.miniloser{width:200px}body{background:linear-gradient(180deg, #18643B 10%, #297a4f 60%, #DEDEC9 100%)}

